@import url('https://maxcdn.bootstrapcdn.com/bootstrap/latest/css/bootstrap.min.css');

.App {
	font-family: sans-serif;
	text-align: center;
}

@media screen and (min-width: 900px) {
	.inner-container {
		transform: scale(1.4);
	}
}
.padStyle {
	outline: none !important;
	border: none;
	box-shadow: 6px 3px 2px gray;
	height: 50px;
	width: 50px;
	color: #fff;
}
.padStyleInactive {
	background-color: #ccc;
}
.padStyleActive {
	background-color: #007bff;
	border-color: #007bff;
}
.padStylePressed {
	color: black;
	background-color: yellow;
	box-shadow: none;
}
.switch {
	position: relative;
	display: inline-block;
	width: 50px;
	height: 24px;
}

.switch input {
	opacity: 0;
	width: 0;
	height: 0;
}

.slider {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #ccc;
	-webkit-transition: 0.4s;
	transition: 0.4s;
}
.sliderBank {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #2196f3;
	-webkit-transition: 0.4s;
	transition: 0.4s;
}

.slider:before {
	position: absolute;
	content: '';
	height: 16px;
	width: 16px;
	left: 4px;
	bottom: 4px;
	background-color: white;
	-webkit-transition: 0.4s;
	transition: 0.4s;
}

.sliderBank:before {
	position: absolute;
	content: '';
	height: 16px;
	width: 16px;
	left: 4px;
	bottom: 4px;
	background-color: white;
	-webkit-transition: 0.4s;
	transition: 0.4s;
}

input:checked + .slider {
	background-color: #2196f3;
}

input:focus + .slider {
	box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before,
input:checked + .sliderBank:before {
	-webkit-transform: translateX(26px);
	-ms-transform: translateX(26px);
	transform: translateX(26px);
}

/* Rounded sliders */
.slider.round,
.sliderBank {
	border-radius: 34px;
}

.slider.round:before,
.sliderBank:before {
	border-radius: 50%;
}
